@import url(https://fonts.googleapis.com/css2?family=Amatic+SC&display=swap);
header{position:fixed;top:0;width:100%;background:linear-gradient(30deg, #13547A, #281531);color:white;z-index:2}header .button-block{text-align:center}header .title{text-align:center;font-family:'Amatic SC', cursive;font-size:40px}

footer{background:#1a1616;width:100%;color:white;position:fixed;bottom:0}footer .title{text-align:center;font-family:'Amatic SC', cursive;font-size:25px}

.main_page .sort{margin-bottom:1rem;margin-left:6rem}.main_page .card_list{height:75vh;overflow:scroll;background:rgba(0,0,0,0.705);border-radius:3px;border:6px solid rgba(0,0,0,0);margin:0 5rem}.main_page .card_list .book_card{background:white;margin:1rem;padding:0.5rem;border-radius:3px}.main_page .card_list .book_card .image_container{width:100%;overflow:hidden;border-right:2px black solid}.main_page .card_list .book_card .image_container img{display:block;height:16rem;margin:.5rem auto;overflow:hidden;box-shadow:0 0 10px rgba(0,0,0,0.5)}.main_page .card_list .book_card .card_buttons{padding:2rem 0 1rem}.main_page .card_list .book_card .card_buttons button{margin-right:1rem}

.modal_background{z-index:2;position:fixed;top:0;left:0;height:100%;width:100%;background:rgba(0,0,0,0.829)}.modal_background .modal_window{background:#ffffff;position:absolute;top:50%;left:50%;width:40%;transform:translate(-50%, -50%);padding:4rem;border-radius:5px}.modal_background .modal_window button{margin:.3rem}

.add_book_page .add_book_form{display:flex;flex-direction:column;padding:1.5rem 5rem;border-radius:.5rem;background:white;margin:0 5rem}.add_book_page .add_book_form label{display:flex;flex-direction:column}.add_book_page .add_book_form input{margin:0.2rem}.add_book_page .add_book_form img{display:block;width:10vh}

.book_id{margin:6rem}

*{margin:0;padding:0;box-sizing:border-box}body{height:100vh}body:before{content:"";position:fixed;left:0;right:0;top:0;bottom:0;z-index:-2;background-color:black}body:after{content:"";position:fixed;left:0;right:0;top:0;bottom:0;z-index:-1;background-color:black;background-image:url(/kaspersky/static/media/background.cdcb6102.jpg);background-position:center;background-size:cover;background-repeat:no-repeat;-webkit-filter:blur(6px);filter:blur(6px)}body section{background-color:rgba(255,255,255,0.671);height:100vh}body section h1{padding:5rem 0 1rem;text-align:center}body img{display:block}

